import React, { useState } from 'react';
import {
  deployPassword,
  deployToken,
  Hash,
} from '../blockchain/functions/functions';
import {
  Container,
  Button,
  Text,
  Input,
  NumberInput,
  NumberInputField,
} from '@chakra-ui/react';

function Deployer() {
  const [tokenInfo, setTokenInfo] = useState({
    name: '',
    symbol: '',
  });
  const [passwordInfo, setPasswordInfo] = useState({
    password: '',
    toknLimit: '',
    addrLimit: '',
    tknAddr: '',
  });
  const [hash, setHash] = useState({
    text: '',
    result: '',
  });

  const handleDeployToken = async () => {
    let receipt = await deployToken(tokenInfo.name, tokenInfo.symbol);
    if (receipt) {
      window.alert(
        `Token Deployed to the Address: ${receipt.events.tokenDeploy.returnValues.newTokenAddress}... Please Save this Address.`
      );
      console.log(receipt);
    }
  };
  const handleDeployPassword = async () => {
    if (checkFields()) {
      let receipt = await deployPassword(
        passwordInfo.password,
        passwordInfo.toknLimit,
        passwordInfo.addrLimit,
        passwordInfo.tknAddr
      );
      if (receipt) {
        console.log(receipt);
        window.alert(
          `Contract Deployed to the Address: ${receipt.events.passwordDeploy.returnValues.newPasswordAddress}... Please Save this Address.`
        );
      }
    }
  };
  const handleHash = async () => {
    let result = await Hash(hash.text);
    if (result) {
      setHash({ ...hash, result: result });
    }
  };

  const checkFields = () => {
    let { password, toknLimit, addrLimit, tknAddr } = passwordInfo;
    if (
      password !== '' &&
      toknLimit !== '' &&
      addrLimit !== '' &&
      tknAddr !== ''
    ) {
      return true;
    } else {
      window.alert('please complete all the fields');
      return false;
    }
  };

  return (
    <Container maxW="container.xl">
      <Container maxW="container.sm" marginBottom="5">
        <Text>Deploy Token</Text>
        <Input
          placeholder="Name"
          value={tokenInfo.name}
          onChange={e =>
            setTokenInfo({
              ...tokenInfo,
              name: e.target.value,
            })
          }
        />
        <Input
          placeholder="Symbol"
          value={tokenInfo.symbol}
          onChange={e =>
            setTokenInfo({
              ...tokenInfo,
              symbol: e.target.value,
            })
          }
        />
        <Button onClick={handleDeployToken}>Deploy</Button>
      </Container>
      <Container maxW="container.sm" marginBottom="5">
        <Text>Deploy Password Contract</Text>
        <Input
          placeholder="Password"
          value={passwordInfo.password}
          onChange={e =>
            setPasswordInfo({
              ...passwordInfo,
              password: e.target.value,
            })
          }
        />
        <NumberInput>
          <NumberInputField
            placeholder="Token Limit"
            value={passwordInfo.toknLimit}
            onChange={e =>
              setPasswordInfo({
                ...passwordInfo,
                toknLimit: e.target.value,
              })
            }
          />
        </NumberInput>
        <NumberInput>
          <NumberInputField
            placeholder="Address Limit"
            value={passwordInfo.addrLimit}
            onChange={e =>
              setPasswordInfo({
                ...passwordInfo,
                addrLimit: e.target.value,
              })
            }
          />
        </NumberInput>
        <Input
          placeholder="Token Address"
          value={passwordInfo.tknAddr}
          onChange={e =>
            setPasswordInfo({ ...passwordInfo, tknAddr: e.target.value })
          }
        />
        <Button onClick={handleDeployPassword}>Deploy</Button>
      </Container>
      <Container maxW="container.sm" marginBottom="5">
        <Text>Text to Hash</Text>
        <Input
          placeholder="Text to Convert"
          value={hash.text}
          onChange={e => setHash({ ...hash, text: e.target.value })}
        />
        <Input placeholder="Result" value={hash.result} readOnly />
        <Button onClick={handleHash}>Hash</Button>
      </Container>
    </Container>
  );
}

export default Deployer;
